body {
  font-family: Arial;
}

.fluid {
  max-width: 1200px;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.fluid__instructions {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  margin: 0 20px;
}

a {
  color: black;
}

a:hover {
  color: #666;
}

.code {
  font-family: Courier New, Courier, monospace;
}

@media (min-width: 480px) {
  .fluid {
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .fluid__image-container{
    -ms-flex: 0 0 30%;
        flex: 0 0 30%;
    margin: 20px 0 20px 20px;
  }

  .fluid__instructions {
    -ms-flex: 0 0 50%;
        flex: 0 0 50%;
    padding-top: 30px;
  }
}



